<template>
  <div id="bar" style="width: 100%; height: 100%"></div>
</template>

<script type="text/javascript">
export default {
  data() {
    return {
      data: [
        { product: "1", 2017: 43, 2018: 85, 2019: 93 },
        { product: "2", 2017: 83, 2018: 73, 2019: 55 },
        // {product: '3', '2015': 80, '2016': 65, '2017': 84 ,'2018': 88, '2019': 95,},
        // {product: '4', '2015': 79, '2016': 52, '2017': 50 ,'2018': 88, '2019': 95}
      ],
    };
  },

  props: {},
  mounted() {
    this.ready();
  },
  methods: {
    ready: function () {
      var echarts = require("echarts");
      // 基于准备好的dom，初始化echarts实例
      var myChart = echarts.init(document.getElementById("bar"), "white");
      // 绘制图表
      var option = {
        legend: {},
        tooltip: {},
        dataset: {
          dimensions: ["product", "2017", "2018", "2019"],
          source: [
            { product: "渣土车", 2017: 43, 2018: 85, 2019: 93 },
            { product: "新型渣土车", 2017: 83, 2018: 73, 2019: 55 },
            // {product: '', '2015': 86, '2016': 65, '2017': 82},
            // {product: '', '2015': 72, '2016': 53, '2017': 39}
          ],
        },
        xAxis: [
          {
            type: "category",
            axisLabel: {
              textStyle: {
                color: "white", //坐标值得具体的颜色
              },
            },
          },
        ],
        yAxis: {
          axisLabel: {
            textStyle: {
              color: "white", //坐标值得具体的颜色
            },
          },
        },
        // Declare several bar series, each will be mapped
        // to a column of dataset.source by default.
        series: [{ type: "bar" }, { type: "bar" }, { type: "bar" }],
      };

      myChart.setOption(option);
    },
  },
};
</script>

<style>
</style>



