<template>
  <div id="snbar" style="width: 100%; height: 100%"></div>
</template>

<script type="text/javascript">
export default {
  data() {
    return {
      data: [],
    };
  },

  props: {},
  mounted() {
    this.ready();
  },
  methods: {
    ready: function () {
      var echarts = require("echarts");
      // 基于准备好的dom，初始化echarts实例
      var myChart = echarts.init(document.getElementById("snbar"), "white");
      // 绘制图表
      var option = {
        color: ["rgb(195,53,49)", "rgb(97,160,168)", "rgb(130,120,87)"],
        title: {
          // text: '某站点用户访问来源',
          // subtext: '纯属虚构',
          left: "center",
        },
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b} : {c} ({d}%)",
        },
        // legend: {
        //     orient: 'vertical',
        //     left: 'left',
        //     data: ['深圳某运输公司',  '深圳一运输公司','深圳永恒工程开发有限公司']
        // },
        series: [
          {
            name: "需求车辆数",
            type: "pie",
            radius: "55%",
            center: ["50%", "60%"],
            data: [
              { value: 335, name: "深圳永恒公司" },
              { value: 310, name: "深圳某运输公司" },
              { value: 234, name: "深圳一运输公司" },
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
          },
        ],
      };
      myChart.setOption(option);
    },
  },
};
</script>

<style>
</style>



