<template>
  <div id="carscatter" style="width: 100%; height: 100%"></div>
</template>

<script type="text/javascript">
import axios from "axios";
export default {
  data() {
    return {
      data: [[]],
      data1: [[]],
      data99: [],
      yes: [],
      no: [],
    };
  },

  mounted() {
    this.ready();
  },
  methods: {
    ready: function () {
      // axios({
      // headers: {
      //     token: this.$store.state.token
      // },
      // method: "get",
      // url:'http://localhost:8877/sourceInfo/si'
      // })
      // .then(response => {
      //     for(var i=0;i<response.data.data.length;i++){
      //         //toFixed(2)四舍五入保留小数点后两位
      //         console.log('获得经纬度'+JSON.stringify(response.data.data))
      //         //  this.data[i]=[response.data.data[i]['sourceLong'].toFixed(2),response.data.data[i]['sourceLat'].toFixed(2)]
      //         this.data[i]=[response.data.data[i]['sourceLong'],response.data.data[i]['sourceLat']]
      //     };
      // console.log('1111'+JSON.stringify(this.data))

      this.data99 = [
        [10.0, 8.04],
        [8.0, 6.95],
        [13.0, 7.58],
        [9.0, 8.81],
        [11.0, 8.33],
        [14.0, 9.96],
        [6.0, 7.24],
        [4.0, 4.26],
        [12.0, 10.84],
        [7.0, 4.82],
        [5.0, 5.68],
      ];
      var echarts = require("echarts");
      // 基于准备好的dom，初始化echarts实例
      var myChart = echarts.init(
        document.getElementById("carscatter"),
        "white"
      );
      // 绘制图表
      var option = {
        title: {
          // text: '产生源垃圾类型占比饼图',
          //subtext: '数据来源：深圳市'
        },
        legend: {
          textStyle: {
            color: "white",
          },
        },
        tooltip: {
          //c表示数值
          formatter: "经纬度: ({c})",
        },

        xAxis: {
          type: "value",
          // minInterval: 1,
          name: "经度",
          nameTextStyle: {
            color: "white",
            fontSize: 14,
          },
          axisLabel: {
            textStyle: {
              fontSize: 14,
              color: "white",
            },
          },
          min: function (value) {
            return value.min;
          },
          max: function (value) {
            return value.max;
          },
          splitLine: {
            show: false,
          },
        },
        yAxis: {
          type: "value",
          name: "纬度",
          nameTextStyle: {
            color: "white",
            fontSize: 14,
          },
          axisLabel: {
            textStyle: {
              fontSize: 14,
              color: "white",
            },
          },
          min: function (value) {
            return value.min;
          },
          max: function (value) {
            return value.max;
          },
          splitLine: {
            show: false,
          },
        },
        // dataset:{
        // source:this.data
        //     } ,
        series: [
          {
            symbolSize: 20,
            // name:'垃圾排放状态',
            type: "scatter",
            encode: {
              x: "x",
              y: "y",
            },
            data: this.data99,
          },
        ],
      };
      myChart.setOption(option);
      // }
      // ).catch(error => console.log(error))
    },
  },
};
</script>

<style>
</style>



